@import url(https://fonts.googleapis.com/css?family=Permanent+Marker|Staatliches|Titillium+Web&display=swap);
.movies {
  display: grid;
  justify-content: space-around;
  margin: 0px 70px;
  padding: 50px;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 320px;
  grid-gap: 80px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie {
  display: flex;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 15px;
  border-radius: 3px;
}

.movie img {
  position: relative;
  top: -40px;
  width: 150px;
  height: 70%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.movie img:hover {
  opacity: 0.8;
}

.movie__data {
  padding: 13px 25px;
}

.movie__data .movie__title {
  font-weight: 600;
  font-size: 20px;
  margin-right: 8px;
}

.movie__data .movie__year {
  color: rgba(0, 0, 0, 0.6);
  font-size: 13px;
}

.movie__data .movie_genres {
  display: flex;
  list-style: none;
  font-size: 15px;
  padding: 0px;
  margin: 0px;
  margin-top: 3px;
  color: rgba(0, 0, 0, 0.6);
}

.movie_genres .genre:not(:last-child) {
  margin-right: 5px;
}

.movie_genres .genre:not(:last-child)::after {
  content: ',';
}

.movie__rating .star {
  color: #ffd32a;
}

.movie__rating .emptyStar {
  color: #bdc3c7;
}

* {
  box-sizing: border-box;
}

body,
html,
.container {
  height: 100%;
  font-family: 'Titillium Web';
}

body {
  margin: 0;
  padding: 0;
  background-image: url('http://hdqwalls.com/download/1/joker-movie-2019-poster-0n-1920x1080.jpg');
  background-position: center;
  background-size: cover;
}

.layer {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4020px;
}

.loader {
  width: 100%;
  height: 100%;
  font-size: 18px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

